@import url(https://fonts.googleapis.com/css?family=Concert+One&display=swap);
.game {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.board {
	display: grid;
	grid-template-columns: calc(100% / 4) calc(100% / 4) calc(100% / 4) calc(100% / 4);
	grid-template-rows: calc(100% / 4) calc(100% / 4) calc(100% / 4) calc(100% / 4);
	width: 350px;
	height: 350px;
}

.board .button {
	font-size: 45px;
}

.button {
	border: 0;
	outline: 0 none;
	font-size: 22px;
	text-transform: uppercase;
	padding: 0.5rem 1rem;
}

.button.styled {
	border-radius: 2px;
	margin: 2px;
}

.button.empty {
	background-color: transparent;
}

.button.correct {
	-webkit-transition: all .2s ease-in;
	transition: all .2s ease-in;
}

.button.error {
	-webkit-filter: grayscale(100%);
	        filter: grayscale(100%);
}

.button.finished:nth-of-type(1) {
	border-radius: 4px 0 0 0;
}

.button.finished:nth-of-type(4) {
	border-radius: 0 4px 0 0;
}

.button.finished:nth-of-type(12), .button.finished:nth-of-type(15) {
	border-radius: 0 0 4px 0;
}

.button.finished:nth-of-type(13) {
	border-radius: 0 0 0 4px;
}

.button.button-theme {
	width: 40px;
	height: 40px;
	background-size: 200%;
}

.button.button-theme.active {
	box-shadow: 0 0 0 2px #FFF !important;
}

.button:not(.empty) {
	cursor: pointer;
	text-shadow: 1px 1px 0px rgba(255, 255, 255, .3), -1px -1px 0px rgba(0, 0, 0, .1);
	color: #FFF;
	font-family: 'Concert One', cursive;
	background-color: #2196F3;
}

.button:not(.empty).styled {
	box-shadow: 0px -4px 0px 0px rgba(0, 0, 0, .1) inset;
}

.status {
	width: 350px;
	margin-top: 1rem;
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.finish {
	display: flex;
	align-items: center;
}

.finish .button {
	margin-left: 1rem;
}
html, body {
	margin: 0;
	overflow: hidden;
}

html, body, #root, .game {
	height: 100%;
}

body {
	background-color: #444444;
	font-family: 'Concert One', cursive;
	color: #FFF;
	font-size: 22px;
}

.made-by {
	position: absolute;
	bottom: 1rem;
	right: 1rem;
	color: rgba(255, 255, 255, .3);
	text-decoration: none;
	font-size: 18px;
}

.made-by:hover {
	color: rgba(255, 255, 255, 1);
}

.choose-theme {
	display: flex;
	justify-content: center;
	margin-bottom: 1rem;
}

.choose-theme .button {
	margin: 0 0.5rem;
}
